.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
            user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
  }
  
  .slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;
  }
  
  .slick-list:focus {
    outline: none;
  }
  
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand;
  }
  
  .slick-slider .slick-track,
  .slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  
  .slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
  }
  
  .slick-track:before,
  .slick-track:after {
    display: table;
    content: '';
  }
  
  .slick-track:after {
    clear: both;
  }
  
  .slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    display: none;
  }
  
  [dir='rtl'] .slick-slide {
    float: right;
  }
  
  .slick-slide img {
    display: block;
  }
  
  .slick-slide.slick-loading img {
    display: none;
  }
  
  .slick-slide.dragging img {
    pointer-events: none;
  }
  
  .slick-initialized .slick-slide {
    display: block;
  }
  
  .slick-loading .slick-slide {
    visibility: hidden;
  }
  
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
  
  .slick-arrow {
    position: absolute;
    top: 50%;
    display: block;
    width: 30px;
    height: 30px;
    margin-top: -15px;
    border: none;
    outline: none;
    background: transparent;
    z-index: 1;
    cursor: pointer;
  }
  
  .slick-prev {
    background-image: url(https://www.freeiconspng.com/thumbs/arrow-icon/right-arrow-icon-27.png);
    background-size: contain;
    filter: invert(100%);
    transform: rotate(180deg);
    opacity: 50%;
    color: rgb(217, 227, 237, 0);
    left: 10px;
  }
  .slick-prev:hover {opacity: 100%;}
  .slick-next:hover {opacity: 100%;}
  .slick-next {
    background-image: url(https://www.freeiconspng.com/thumbs/arrow-icon/right-arrow-icon-27.png);
    background-size: contain;
    filter: invert(100%);
    opacity: 50%;
    right: 10px;
    color: rgb(217, 227, 237, 0);
  }
  
  .slick-arrow:hover:before,
  .slick-arrow:focus:before {
    opacity: 1;
  }
  
  .slick-arrow:before {
    font-family: 'slick';
    font-size: 30px;
    line-height: 1;
    opacity: 0.75;
    color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .slick-arrow.slick-disabled:before {
    opacity: 0.25;
  }
  
  .slick-dots {
    position: absolute;
    bottom: 0;
    width: 100%;
    margin: 0;
    list-style: none;
    text-align: center;
  }
  
  .slick-dots li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
  }
  
  .slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
    }
    
    .slick-dots li button:hover,
    .slick-dots li button:focus {
    outline: none;
    }
    
    .slick-dots li button:hover:before,
    .slick-dots li button:focus:before {
    opacity: 1;
    }
    
    .slick-dots li button:before {
    font-family: 'slick';
    font-size: 6px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: '•';
    text-align: center;
    opacity: 0.25;
    color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    }

    
    .slick-dots li.slick-active button:before {
    opacity: 0.75;
    }
    .carousel-value{
      background-color: rgb(230, 230, 230, 0.9) ;
      width: 100%;
      height: 300px;
      background-size: cover;
      background-position: center;
    }
   
    @media (max-width: 767px) {
    .slick-dots {
    display: none !important;
    }
    }
  