@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');
@import url('https://fonts.googleapis.com/css?family=Spartan');

*,
html {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
  box-sizing: border-box;
}
body{
  background-color: rgb(193, 192, 192, 0.2);
  height: 100vh;
}
.main{
  height: 200vh;
  -webkit-user-select: none; 
  -ms-user-select: none; 
  user-select: none; 
}
#root{
  max-width: 800px;
}
/* scroll */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
/* nav css */
.ruftop{
  height: 85px;
}
.icon{
  background-image: url(https://pbs.twimg.com/media/Fs7PQNFaIAIKsGR?format=webp);
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.Main-icon{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.Main-icon > div{
  color: gray ;
  font-weight: 600;
}
.nav-index{
  width: 100%;
  z-index:-1;
  position:fixed;
  border-bottom: 1px solid rgb(193, 192, 192);
  background-color: rgb(230, 230, 230, 0.9);
  height: 81px;
}
.Nav-main{
  max-width: 800px;
  width: 100%;
  z-index:50;
  position:fixed;
}
.Nav-kota{
  text-decoration: none;
  list-style: none;
  color: gray;
  padding: 8px;
  padding-top: 3px;
  padding-bottom: 1px;
  border-bottom:  rgb(193, 192, 192, 0) solid 3px;
}
.link-nav{text-decoration: none;}
.Nav-kota:hover{
  background-color: rgb(193, 192, 192, 0.2);
  transition: ease 0.3s;
  border-radius: 5px 5px 0 0 ;
  text-decoration: underline;
}
.on-kota{
  background-color: rgb(193, 192, 192, 0.2);
  transition: ease 0.3s;
  border-radius: 5px 5px 0 0 ;
}
.Nav-bg{
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(193, 192, 192);
  background-color: rgb(230, 230, 230, 0.9);
}
.Nav-v{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  cursor: pointer ;
}
/* header */
.header{
  display: flex;
  justify-content: left;
  margin: 10px;
}
.header > h2{
  font-weight: 400;
  color:#555;
  font-family: 'Spartan';
  font-size: 20px; 
  font-weight: 600;
}
/* daftar css */
.daftar{
  margin:10px;
  height: 600px;
  width: auto;
  background-color: rgb(230, 230, 230, 0.9);
  border-radius: 2px;
  overflow: scroll;
  overflow-x: hidden;
  justify-content: left;
}
.daftar::-webkit-scrollbar {
  display: none;
}
.daftar-value{
  height: 100px;
  width: 100px;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 8px 0 0 8px;
}
.daftar-value-main{
  display: flex;
}
.daftar-title{
  margin: 10px;
  font-weight: 400;
}
.daftar-detail > section{
  display: flex;
  justify-content: left;
  margin-left: 10px;
}
.daftar-detail > section > button{
  padding: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  border-radius: 5px;
  background-color: inherit;
}
.daftar-selengkapnya{
  height: 25px;
  transform: translateY(-35px);
  border-radius: 0 0 8px 0;
  margin-left: 130px;
  margin-right: 10px;
  display: flex;
  justify-content: right;
}
.daftar-selengkapnya > div{
  height: 15px;
  width: 15px;
  background-image: url(https://www.pngmart.com/files/3/Right-Arrow-PNG-Transparent-Picture.png);
  background-size: contain;
}
.daftar-bg{
  width: 100%;
  height: 120px;
  cursor: pointer;
}
.daftar-bg:hover{
  filter: brightness(50%);
  background-color: rgb(230, 230, 230, 0.3);
}


@media only screen and (max-width: 550px) {
  * {
    cursor:default;
  }
}
